<template>
  <!--     配置管理 -->
  <div class="allocation-body">
    <div class="allocation-header">
      <a-breadcrumb separator=">">
        <a-breadcrumb-item>配置管理</a-breadcrumb-item>
      </a-breadcrumb>
      <hr />
    </div>
    <div class="allocation-section">
      <span>工厂退换货地址管理</span>
      <div>
        <span v-show="item == ''">当前未设置退换货地址</span>
        <span>{{ item.merName }}</span>
        <span>{{ item.mobile }}</span>
        <span>{{ item.address }}</span>
        <a-button v-show="item == ''" type="primary" @click="showModal">
          去设置
        </a-button>
        <a-button type="primary" v-show="item != ''" @click="showModal">
          去修改
        </a-button>
        <a-modal
          title="添加地址"
          :visible="visible"
          :confirm-loading="confirmLoading"
          @ok="handleOk"
          @cancel="handleCancel"
          okText="提交"
          cancelText="取消"
        >
          <p><span>收货人：</span><input type="text" v-model="name" /></p>
          <p><span>手机号：</span><input type="text" v-model="mobile" /></p>
          <p><span>地址：</span><input type="text" v-model="address" /></p>
        </a-modal>
      </div>
    </div>
    <div class="allocation-footer"></div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visible: false,
      confirmLoading: false,
      name: "",
      mobile: "",
      address: "",
      item: [],
    };
  },
  mounted() {
    this.list();
  },
  methods: {
    showModal() {
      this.visible = true;
    },
    handleOk(e) {
      this.confirmLoading = true;
      setTimeout(async () => {
        this.visible = false;
        this.confirmLoading = false;
        let str = {
          merName: this.name,
          mobile: this.mobile,
          address: this.address,
          merId: sessionStorage.getItem("id"),
        };
        let { data } = await this.$http.addmodlfy(str);
           console.log(data);

        if (data.code == 200) {
          this.list();
          this.name = "";
          this.mobile = "";
          this.address = "";
        }
      }, 2000);
    },
    handleCancel(e) {
      console.log("Clicked cancel button");
      this.visible = false;
    },
    async list() {
      let str = {
        merId: sessionStorage.getItem("id"),
      };
      let { data } = await this.$http.meraddress(str);
          console.log(data);
      if (data.code == "0000") {
        this.item = data.data;
      } else {
        this.item = "";
      }
    },
  },
};
</script>

<style scoped>
.allocation-section {
  width: 100%;
  padding: 10px 100px;
}
.allocation-section div {
  border: 1px solid #c0c0c0;
  border-top: 0px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 50px 0px 10px;
  height: 50px;
  font-size: 16px;
}
.allocation-section > :nth-child(1) {
  display: block;
  background: #c0c0c0;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  font-weight: 900;
  padding-left: 10px;
}
.ant-modal-body input {
  outline: none;
}

p span {
  text-align: right;
  display: inline-block;
  width: 15%;
}
</style>